.header {
    grid-area: h;
    border-radius: 5px;
    box-shadow: 0px 7px 4px rgba(1, 5, 63, 0.1),
    0px 8px 10px rgb(20 40 80 / 8%);
    color: white;
    background-color: rgb(15, 76, 168);
}

.signOut{
    cursor: pointer;
    padding-right: 30px;
    color: #fff;
}

.header img {
    width: 90px;
    padding-left: 20px;
}

.loginBlock{
    float: right;
    padding: 15px;
}

.loginBlock a{
    color: rgb(1, 2, 50);
}